.header{
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  position: sticky;
  left: 0;
  top: 0;
  z-index: 9999;
  background-color: white;
  &-logo{
    margin-left: 30px;
    @include fz(24);
    
  }
  
  &-list{
    display: flex;
   
  }
  &-item{
    padding-right: 30px;
    @include fz(16);
    &:hover{
      @include _hover;
    }
 
}
// ここからspヘッダー
.sp{
  &-hambugar{
    display: none;
      width: 36px;
      height: 32px;
      position: absolute;
      cursor: pointer;
      z-index: 2;
      top: 20px;
      right: 20px;
      transition: 0.4s;
      span{
        position: absolute;
        left: 2px;
        display: block;
        width: 35px;
        height: 4px;
        background-color:black;
        transition: 0.8s;
        &:nth-of-type(1){
          top: 0;
        }
        &:nth-of-type(2){
          top: 14px;
        }
        &:nth-of-type(3){
          bottom: 0;
      }
  }
  @include mq{
    display: initial;
  }
}
   


  &-background{
    position: absolute;
    top: 0;
    left: -100%;
    background: white;
    z-index: 1;
    width: 100%;
    height: 100vh;
    transition: .7s;
    overflow: hidden;
  }
  &-item{
    @include fz(18);
      text-align: center;
    margin-top: 40px;
    font-weight: normal;
    color:  rgb(0, 51, 204);
  }

} 
.in{
  transform: translateX(100%);
}
.line_1,.line_2,.line_3{
  background: #000000;
}
.line_1 {
  transform: translateY(12px) rotate(-45deg);
  top: 0;
}
.line_2 {
  opacity: 0;
}
.line_3 {
  transform: translateY(-16px) rotate(45deg);
  bottom: 0;
}


@include mq(){
  &-list{
    display: none;
  }
}
   
}