.footer{
  background-color: $bColor;
&-inner{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  color: white;
  padding: 20px 0;
  align-items: center;
  small{
    @include fz(12);
  }
  @include mq{
    padding: 5% 15%;
  }
}
&-nqv{
  width: 100%;
}
&-list{
  display: flex;
  @include mq{
    display: initial;
  }
}
&-item{
 @include fz(12);
 @include mq{
   margin-top: 10px;
 }
 &:after{
   content: '|';
   display: inline-block;
   padding: 0 10px;
   @include mq{
     display: none;
   }
 }
 &:hover{
   @include _hover;
 }
}
}